import React, { useState } from 'react'
// @mui
import { Chip, Stack, Typography, useTheme } from '@mui/material';
// third-party
import { uniq, filter } from 'lodash'
// project
import Menu from 'components/menu'

const ChipGroup = (props) => {
    const theme = useTheme();
    const { caption, options } = props;
    const [chips, setChips] = useState([])

    const handleChips = (caption, newChip) => {
        setChips((preChips) => {
            let items = preChips;
            items = uniq([...items, newChip])
            return items
        })
    };
    const handleChipDelete = (type, deletChip) => {
        // if (type?.toLowerCase()?.indexOf('status') !== -1) {
        //   status = filter(status, (_item) => _item?.indexOf(condition) === -1)
        // }
        setChips(filter(chips, (_item) => _item?.indexOf(deletChip) === -1))
    }

    return (
        <Stack direction={'row'} flexWrap='wrap'>
            {chips?.map((item) => (
                <Chip key={item} label={

                    <Typography color='warning.main'>{item}</Typography>

                }
                    onDelete={() => handleChipDelete(null, item)}
                    sx={{
                        borderRadius: `${theme.shape.borderRadius}px`
                    }}
                />
            ))}
            <Menu options={chips} handleMenuItemChecked={handleChips} caption={caption} items={options} />
        </Stack>
    )
}


export default ChipGroup;